@import "../variables";

.pizza-wrap {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.pizza-block {
  margin-top: 30px;
  width: 280px;
  text-align: center;
  margin-bottom: 65px;

  @media (max-width: 410px) {
    width: 100%;
  }
  &-wrap {
    display: flex;
    justify-content: center;
  }

  &__image {
    width: 260px;
    @media (max-width: 410px) {
      width: 200px;
    }
  }
  &__image:hover {
    transform: scale(1.1);
    transition: all 0.3s ease-in-out;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
    margin-top: 10px;
    @media (max-width: 410px) {
      font-size: 16px;
    }
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;
    @media (max-width: 410px) {
      width: 180px;
      margin: 0 auto;
    }

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
    @media (max-width: 410px) {
      font-size: 18px;
    }
  }
}
